.BuildControls {
    width: 100%;
    background-color: #F2E3BC;
    display: flex;
    flex-flow: column;
    align-items: center;
    box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;
}


.OrderButton {
    background-color: rgb(226, 190, 159);
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #ffffff;
    font-weight: 500;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: 2px 2px 2px #888888;
}

.OrderButton:hover, .OrderButton:active {
    background-color: rgb(127, 161, 161);
    border: 1px solid #966909;
    color: #ffffff;
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
    box-shadow: 2px 2px 2px #888888;
}

.OrderButton:not(:disabled) {
    animation: enable 0.3s linear;
}

@keyframes enable {
    0% {
        transform: scale(1);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}